import { Storage } from "@ionic/storage";
import { Toast } from "@ionic-native/toast";
import { HttpClient } from "@angular/common/http";
import { Component } from "@angular/core";
import { NavController, NavParams, App } from "ionic-angular";
import { Clipboard } from "@ionic-native/clipboard";
import { Mixpanel } from "@ionic-native/mixpanel";


@Component({
  selector: "page-voucher-payment-made",
  templateUrl: "voucher-payment-made.html"
})
export class VoucherPaymentMadePage {
  details: any;

  constructor(
    public navCtrl: NavController,
    public app: App,
    public navParams: NavParams,
    private http: HttpClient,
    private clipboard: Clipboard,
    private toast: Toast,
    private storage: Storage,
    private mixpanel: Mixpanel
  ) {
    this.details = this.navParams.get("details");
    this.initPay();
    this.mixpanel.track("Purchase Voucher", {
      type: this.details.type
    });
  }

  continue() {
    const startIndex = this.navCtrl.getActive().index - 1;
    this.navCtrl.remove(startIndex, 2).then(() => {
      this.navCtrl.setRoot("TabsPage");
    });
  }

  copyVoucher() {
    this.clipboard.copy(this.details.voucher).then(data => {});
    this.toast.showLongBottom("Voucher code copied!").subscribe(() => {});
  }

  initPay() {
    this.http
      .post("https://realm-glory.herokuapp.com/api/voucher/paymemt", {
        user: this.details.user || null,
        type: this.details.type,
        voucher: this.details.voucher
      })
      .subscribe(data => {
        if (data["status"] === "success" && data["data"]) {
          this.storage.set("voucher", data["data"]);
        }
        if (data["status"] === "user" && data["data"]) {
          this.storage.set("user", data["data"]);
        }
      });
  }
}



